@charset "UTF-8";
.text-center {
  text-align: center; }

.cf {
  *zoom: 1; }
  .cf:before, .cf:after {
    content: " ";
    display: table; }
  .cf:after {
    clear: both; }

body .wrapper {
  font-family: 'Roboto', sans-serif;
  color: #f4faff;
  max-width: 70%;
  margin: 0 auto; }
  body .wrapper h1 {
    margin-top: 50px;
    margin-bottom: 50px;
    text-transform: uppercase;
    font-size: 1.5em; }
  body .wrapper .speechbubble {
    background-color: #747580;
    color: #f4faff;
    font-size: 0.9em;
    line-height: 1.75;
    padding: 25px 25px;
    margin-bottom: 50px;
    cursor: default; }
    body .wrapper .speechbubble:nth-child(4n+1) {
      border-color: #ff8750; }
    body .wrapper .speechbubble:nth-child(4n+2) {
      border-color: #01AD9B; }
    body .wrapper .speechbubble:nth-child(4n+3) {
      border-color: #b388dd; }
    body .wrapper .speechbubble:nth-child(4n+4) {
      border-color: #56b273; }
    body .wrapper .speechbubble div:before {
      content: "“";
      font-family: Georgia;
      font-size: 40px;
      line-height: 0;
      display: inline-block;
      display: -webkit-inline-box; }
    body .wrapper .speechbubble div:after {
      content: "”";
      font-family: Georgia;
      font-size: 40px;
      margin-top: 12px;
      line-height: 0;
      display: inline-block;
      display: -webkit-inline-box;
      bottom: 0;
      right: 0;
      margin-right: -4px;
      margin-bottom: -14px; }
    body .wrapper .speechbubble [contenteditable]:focus {
      outline: 0px solid transparent; }
    body .wrapper .speechbubble .username {
      display: inline;
      font-style: italic;
      float: right; }
      body .wrapper .speechbubble .username:before {
        content: '- '; }
  body .wrapper .rightarrow {
    border-right: 5px solid; }
  body .wrapper .rightarrow:after {
    content: '';
    margin-top: -30px;
    padding-top: 0px;
    position: relative;
    bottom: -45px;
    left: 90%;
    border-width: 30px 30px 0 0;
    border-style: solid;
    border-color: #747580 transparent;
    display: block;
    width: 0; }
  body .wrapper .leftarrow {
    border-left: 5px solid; }
  body .wrapper .leftarrow:after {
    content: '';
    margin-top: -30px;
    padding-top: 0px;
    position: relative;
    bottom: -45px;
    left: 20px;
    border-width: 30px 0 0 30px;
    border-style: solid;
    border-color: #747580 transparent;
    display: block;
    width: 0; }
